/* stylelint-disable */
$clr-grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
) !default;
/* stylelint-enable */

$clr-grid-columns: 12 !default;
$clr-grid-gutter-width: $clr_baselineRem_1 !default;

$clr-sizes: () !default;
$clr-sizes: map-merge(
  (
    25: 25%,
